@use "sass:math";
@use "../mixins/mixins" as *;
.footer {
    width: 100%;
    /* background-color: #202020;
    padding: 30px 0 50px 0; */
    /* padding-bottom: 100px; */
    &--inner {
        width: 100%;
        background-color: #202020;
        padding: 30px 0 50px 0;
    }
    &-logo {
        display: block;
        width: 100%;
        height: 100%;
        max-width: 253px;
        max-height: 250px;
        margin: 0 auto;
        margin-bottom: 40px;
    }
    &-nav {
        width: 90%;
        display: flex;
        justify-content: space-between;
        margin: 0 auto;
        transform: translateX(1em);
        @include to-md {
            flex-direction: column;
            align-items: center;
            gap: 40px;
             transform: translateX(0);
        }
        &-item {
            position: relative;
            /* @include to-md {
                width: 200px;
            } */
            &::before {
                content: "";
                display: block;
                position: absolute;
                left: -1.5em;
                top: 50%;
                transform: translateY(-50%);
                $fluid: math.div(19, math.div(1920, 100));
                width: clamp(15px, #{$fluid}vw, 19px);
                height: clamp(15px, #{$fluid}vw, 19px);
                width: 15px;
                height: 15px;

                background-color: #ef4923;
                border-radius: 50%;
            }
        }
        &-link {
            font-family: "Daikon", sans-serif;
            font-weight: 400;
            line-height: normal;
            $font: math.div(38, math.div(1920, 100));
            font-size: clamp(20px, #{$font}vw, 38px);
            color: #d2bba8;
        }
    }
}
